
.posts {
  max-width: 700px;
  margin: 0 auto;
}
.posts h1 {
  margin: 0 0 1em;
}
@media screen and (max-width: 840px) {
.posts {
    margin-left: 0;
}
}
@media screen and (max-width: 1400px) and (min-width: 841px) {
.posts {
    margin-left: 280px;
}
}
.question {
  margin: 2em 0;
  padding-left: 20px;
  border-left: 4px solid #42b983;
  font-weight: 600;
}
.question p {
  margin: 0.5em 0;
}


