
.no-list-style {
  list-style: none;
  padding: 0;
}
.sidebar {
  text-align: left;
  position: fixed;
  left: 0;
  top: 70px;
  bottom: 0;
  z-index: 10;
  padding: 2em 0 50px 65px;
  width: 260px;
  margin-right: 20px;
  overflow-x: hidden;
  overflow-y: auto;
}
.sidebar .search-container {
  display: none;
}
.sidebar .nav-link-container {
  display: none;
}
@media screen and (max-width: 840px) {
.sidebar {
    left: auto;
    right: -270px;
    margin-right: 0;
    padding: 10px 30px 20px;
    background: #f9f9f9;
    top: 59px;
    bottom: 0;
    width: 200px;
    box-shadow: 0 0 4px rgba(0,0,0,0.25);
    transition: right 0.3s ease;
}
.sidebar .search-container {
    display: block;
    float: none;
}
.sidebar .nav-link-container {
    display: block;
    float: none;
}
.sidebar .nav-link-container .nav-link-item {
    display: block;
    margin: 0;
    line-height: 30px;
}
.sidebar .nav-link-container .nav-link-item a:hover,
  .sidebar .nav-link-container .nav-link-item a.router-link-active {
    border-bottom: 2px solid #42b983;
}
.sidebar.show {
    right: 0;
}
.sidebar .question-links-container {
    list-style-type: none;
    margin: 0;
    line-height: 1.8em;
    padding-left: 1em;
}
.sidebar .question-links-container .question-link {
    line-height: 1em;
    font-size: 14px;
    padding-bottom: 1px;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.sidebar .question-links-container .question-link:hover,
  .sidebar .question-links-container .question-link.router-link-active {
    border-bottom: 2px solid #42b983;
}
}
.sidebar .question-links-container {
  padding-left: 0;
  margin: 0;
}
.sidebar .question-links-container .question-link {
  list-style-type: none;
  line-height: 1.8em;
}
.sidebar .question-links-container .question-link a {
  color: #7f8c8d;
}
.sidebar .question-links-container .question-link a:hover,
.sidebar .question-links-container .question-link a.router-link-active {
  color: #42b983;
}
.mask {
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: transparent;
}
@media screen and (min-width: 841px) {
.mask {
    display: none;
}
}


